.remoteWorkBlock {
    height: 300px;
    background-color:#6c757d;
}

.content {
    width: 80%;
    border: 2px solid;
    border-radius: 10px;
    margin: 0 auto;
    display: flex;
    flex-direction: column;
    align-items: center;
    background-color: #bdbdbd ;
}

.button {
    border-radius: 5px;
    background-color: #858792;
    color: blue;
    margin: 5px;
}